<template>
  <div class="about" @scroll="ballscroll(e)">
    <TopBanner />
    <div class="road">
      <img src="../assets/main/bg_4_2.png" alt="bg2">
    </div>
    <div class="footer">
      <img src="../assets/main/bg_4_3.png" alt="bg3">
    </div>
    <div class="box01">
      v-contaner
    </div>
    <v-contaner id="ball" class="ball">
      <img src="../assets/item/balloon01.png" alt="ballon">
    </v-contaner>
  </div>
</template>
<script>
import TopBanner from '@/components/TopBanner.vue'

export default {
  name: 'AboutView',
  created () {
    window.addEventListener('scroll', this.handleScroll)
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  components: {
    TopBanner
  },
  data () {
    return {
      scrollPostion: 0
    }
  },
  methods: {
    handleScroll (e) {
      window.addEventListener('scroll', function () {
        const scroll = this.scrollY
        const ball = document.getElementById('ball')
        ball.style = `transform: translateY(${scroll}px)`
      })
    }
  }
}
</script>

